import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { Button, Spin, Tag, Typography, Steps, Modal } from '@coze-arch/coze-design';
import { IconArrowLeft } from '@coze-arch/bot-icons';
import { I18n } from '@coze-arch/i18n';
import axios from 'axios';
import styles from './index.module.less';

const { Title, Text, Paragraph } = Typography;

interface ScriptStage {
  order: number;
  name: string;
  description: string;
  duration: number;
  bot_ids: number[];
  output_type: string;
  output_template: string;
  weight: number;
}

interface ScriptDetail {
  id: number;
  name: string;
  nameEn?: string;
  difficulty: number;
  duration: number;
  icon: string;
  description: string;
  background: string;
  objectives: string[];
  stages: ScriptStage[];
}

const difficultyMap = {
  1: { text: 'ÁÆÄÂçï', color: 'green' },
  2: { text: '‰∏≠Á≠â', color: 'orange' },
  3: { text: 'Âõ∞Èöæ', color: 'red' },
};

const ScriptDetailPage: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [script, setScript] = useState<ScriptDetail | null>(null);
  const [loading, setLoading] = useState(true);
  const [startModalVisible, setStartModalVisible] = useState(false);

  useEffect(() => {
    fetchScriptDetail();
  }, [id]);

  const fetchScriptDetail = async () => {
    try {
      const response = await axios.get(`/api/edu/scripts/${id}`);
      if (response.data.code === 0) {
        setScript(response.data.data);
      }
    } catch (error) {
      console.error('Failed to fetch script detail:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleStartScript = async () => {
    try {
      const response = await axios.post('/api/edu/projects', {
        script_id: Number(id),
        title: `${script?.name} - È°πÁõÆ`,
      });

      if (response.data.code === 0) {
        const projectId = response.data.data.project_id;
        navigate(`/edu/workspace/${projectId}`);
      }
    } catch (error) {
      console.error('Failed to start script:', error);
    }
  };

  if (loading) {
    return <Spin size="large" style={{ display: 'flex', justifyContent: 'center', padding: '100px' }} />;
  }

  if (!script) {
    return <div>Script not found</div>;
  }

  return (
    <div className={styles.container}>
      {/* ËøîÂõûÊåâÈíÆ */}
      <Button
        icon={<IconArrowLeft />}
        type="tertiary"
        onClick={() => navigate('/edu/scripts')}
        className={styles.backButton}
      >
        {I18n.t('common_back', {}, 'ËøîÂõû')}
      </Button>

      {/* Â§¥ÈÉ®‰ø°ÊÅØ */}
      <div className={styles.headerCard}>
        <div className={styles.header}>
          <span className={styles.icon}>{script.icon}</span>
          <div className={styles.headerContent}>
            <div className={styles.titleRow}>
              <Title heading={2} className={styles.title}>
                {script.name}
              </Title>
              <Tag color={difficultyMap[script.difficulty as 1 | 2 | 3].color}>
                {difficultyMap[script.difficulty as 1 | 2 | 3].text}
              </Tag>
            </div>
            {script.nameEn && (
              <Text type="secondary">{script.nameEn}</Text>
            )}
            <Paragraph type="secondary" className={styles.description}>
              {script.description}
            </Paragraph>
            <div className={styles.meta}>
              <Text type="tertiary">
                ‚è±Ô∏è È¢ÑËÆ°Êó∂ÈïøÔºö{script.duration} ÂàÜÈíü
              </Text>
            </div>
          </div>
        </div>

        <Button
          type="primary"
          size="large"
          onClick={() => setStartModalVisible(true)}
          className={styles.startButton}
        >
          {I18n.t('edu_start_script', {}, 'ÂºÄÂßãÂâßÊú¨')}
        </Button>
      </div>

      {/* ËÉåÊôØÊïÖ‰∫ã */}
      <div className={styles.section}>
        <Title heading={4}>{I18n.t('edu_background', {}, 'ËÉåÊôØÊïÖ‰∫ã')}</Title>
        <Paragraph style={{ whiteSpace: 'pre-wrap' }}>
          {script.background}
        </Paragraph>
      </div>

      {/* Â≠¶‰π†ÁõÆÊ†á */}
      <div className={styles.section}>
        <Title heading={4}>{I18n.t('edu_objectives', {}, 'Â≠¶‰π†ÁõÆÊ†á')}</Title>
        <ul className={styles.objectivesList}>
          {script.objectives.map((objective, index) => (
            <li key={index}>
              <Text>{objective}</Text>
            </li>
          ))}
        </ul>
      </div>

      {/* Èò∂ÊÆµÊµÅÁ®ã */}
      <div className={styles.section}>
        <Title heading={4}>{I18n.t('edu_stages', {}, 'Èò∂ÊÆµÊµÅÁ®ã')}</Title>
        <Steps direction="vertical" current={-1}>
          {script.stages.map((stage, index) => (
            <Steps.Step
              key={index}
              title={`Èò∂ÊÆµ ${stage.order}: ${stage.name}`}
              description={
                <div className={styles.stageDescription}>
                  <Text type="secondary">{stage.description}</Text>
                  <div className={styles.stageMeta}>
                    <Text type="tertiary" size="small">
                      ‚è±Ô∏è {stage.duration} ÂàÜÈíü
                    </Text>
                    <Text type="tertiary" size="small">
                      üìù ‰∫ßÂá∫Ôºö{stage.output_template}
                    </Text>
                  </div>
                </div>
              }
            />
          ))}
        </Steps>
      </div>

      {/* Á°ÆËÆ§ÂºÄÂßãÂºπÁ™ó */}
      <Modal
        title={I18n.t('edu_confirm_start', {}, 'Á°ÆËÆ§ÂºÄÂßãÂâßÊú¨')}
        visible={startModalVisible}
        onOk={handleStartScript}
        onCancel={() => setStartModalVisible(false)}
        okText={I18n.t('common_confirm', {}, 'Á°ÆËÆ§')}
        cancelText={I18n.t('common_cancel', {}, 'ÂèñÊ∂à')}
      >
        <Paragraph>
          {I18n.t('edu_start_confirm_message', {}, 'Á°ÆËÆ§ÂºÄÂßã„Ää{name}„ÄãÂâßÊú¨ÂêóÔºü', { name: script.name })}
        </Paragraph>
        <Paragraph type="secondary">
          {I18n.t('edu_start_tip', {}, 'ÂºÄÂßãÂêéÔºåÁ≥ªÁªüÂ∞Ü‰∏∫‰Ω†ÂàõÂª∫‰∏Ä‰∏™Êñ∞ÁöÑÈ°πÁõÆÔºå‰Ω†ÂèØ‰ª•Âú®È°πÁõÆ‰∏≠‰∏éÊô∫ËÉΩ‰ΩìÂçè‰ΩúÂÆåÊàê‰ªªÂä°„ÄÇ')}
        </Paragraph>
      </Modal>
    </div>
  );
};

export default ScriptDetailPage;
