import { useState, useCallback } from 'react';
import axios from 'axios';

interface ScriptQuery {
  keyword?: string;
  difficulty?: number | null;
  page?: number;
  page_size?: number;
}

interface ScriptItem {
  id: number;
  name: string;
  nameEn?: string;
  difficulty: number;
  duration: number;
  icon: string;
  description: string;
  createdAt: string;
}

interface ScriptListData {
  list: ScriptItem[];
  total: number;
  page: number;
  page_size: number;
}

export const useScriptList = () => {
  const [data, setData] = useState<ScriptListData | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<Error | null>(null);

  const fetchScripts = useCallback(async (query: ScriptQuery = {}) => {
    setLoading(true);
    setError(null);

    try {
      const response = await axios.get('/api/edu/scripts', {
        params: {
          keyword: query.keyword || '',
          difficulty: query.difficulty || undefined,
          page: query.page || 1,
          page_size: query.page_size || 20,
        },
      });

      if (response.data.code === 0) {
        setData(response.data.data);
      } else {
        throw new Error(response.data.msg || 'Failed to fetch scripts');
      }
    } catch (err) {
      setError(err as Error);
      console.error('Failed to fetch scripts:', err);
    } finally {
      setLoading(false);
    }
  }, []);

  return {
    data,
    loading,
    error,
    fetchScripts,
  };
};
